.soon-app {
  position: relative;
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
}

.soon-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: auto;
  padding: auto;
}

.soon-text-title {
  font-family: "ComicR";
  font-size: 5vw;
  color: #fff;
  margin: 0;
  padding: 0;
}


/* SCREEN SIZE */

@media (max-width:768px){

  .soon-app {
    position: relative;
    width: 100vw;
    height: 100vh;
    background-color: #000;
    margin: 0;
    padding: 0;
  }
  
  .soon-container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
  
  .soon-text-title {
    font-family: "ComicR";
    font-size: 10vw;
    color: #fff;
    margin: 0;
    padding: 0;
  }
}