.menu-app {
  display: block;
  position: fixed;
  z-index: 100;
  top: 0;
  right: 0;
  width: 35%;
  height: 100%;
  background-color: rgba(17, 16, 16, 0.9);
  backdrop-filter: blur(15px);
}

.menu-container {
  position: relative;
  width: 100%;
  height: 100vh;
  padding: 0% 2%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}


/* MENU TITLE */
.menu-title-container {
  height: 5vw;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: row;
}
  
.menu-title-text {
  font-family: "ComicR";
  font-size: 3vw;
  color: #eee;
  padding: 0;
  margin: 0;
}
  
.menu-title-slash {
  font-family: "ComicI";
  font-size: 3.5vw;
  color: rgba(150, 15, 13, 1.0);
  padding: 0;
  margin: 0;
  transform: translate(-0.5vw, 0.25vw);
}
  
.menu-title-description {
  height: 100%;
  font-family: "ComicR";
  font-weight: bold;
  font-size: 1.75vw;
  letter-spacing: 5px;
  color: rgba(150, 15, 13, 1.0);
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  transform: translate(-1vw, 0);
}


/* LINK */

.menu-link {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #222;
}

.menu-link:hover {
  background-color: rgba(250, 250, 250, 0.05);
}

.menu-link-text {
  text-align: left;
  font-family: "ComicR";
  font-size: 2vw;
  line-height: 0.5vw;
  color: #fff;
  margin: 0;
  padding: 2vw 0;
}


/* SOCIAL LINK */

.menu-link-social-container {
  width: 100%;
  height: 5vw;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.menu-link-social {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 2vw;
}

.menu-link-social:hover {
  background-color: rgba(250, 250, 250, 0.05);
}

.menu-link-social-img {
  width: 2.6vw;
  height: 100%;
}


/* SOON */

.menu-next {
  width: 100%;
}

.menu-next-text {
  text-align: center;
  font-family: "ComicR";
  font-size: 2vw;
  color: #fff;
  line-height: 2vw;
}



/* SCREEN SIZE */

@media (max-width:768px){

  .menu-app {
    width: 100%;
    height: 100%;
    padding-top: 15vw;
    overflow: scroll;
  }
  
  .menu-container {
    width: 100%;
    height: 100vh;
    padding: 0% 0.5%;
    justify-content: flex-start;
  }

  .menu-title-container {
    display: none;
  }

  .menu-link-text {
    text-align: left;
    font-family: "ComicR";
    font-size: 8vw;
    line-height: 10vw;
    color: #fff;
    margin: 0;
    padding-bottom: 4vw;
  }

  .menu-link-social-container {
    width: 100%;
    height: 20vw;
  }
  
  .menu-link-social-img {
    width: 10vw;
    height: 100%;
  }

  .menu-next-text {
    text-align: center;
    font-family: "ComicR";
    font-size: 6vw;
    color: #fff;
    line-height: 6vw;
  }
}