body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: "ComicR";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #111010;
}

.App {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}


@font-face {
  font-family: "Tokyo";
  src: url("./assets/fonts/Tokyo-Regular.ttf") format("truetype");
  font-style: normal;
}

@font-face {
  font-family: "ComicI";
  src: url("./assets/fonts/Comic-Book-Italic.otf") format("truetype");
  font-style: normal;
}

@font-face {
  font-family: "ComicR";
  src: url("./assets/fonts/Comic-Book-Regular.otf") format("truetype");
  font-style: normal;
}

@font-face {
  font-family: "Cavolini";
  src: url("./assets/fonts/Cavolini.ttf") format("truetype");
  font-style: normal;
}