.about-app {
  position: relative;
  width: 100vw;
  height: 100%;
  background-color: #111010;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.about-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.about-container-right {
  position: relative;
  width: 80%;
  height: 60vw;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding-right: 10%;
}

.about-container-left {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 0% 5%;
  padding-bottom: 5%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}


/* CHINESE SIGN */

.about-container-chn {
  position: absolute;
  height: 25vw;
  right: 2vw;
}


/* IMAGES */

.about-container-right-img-bg {
  position: absolute;
  width: 40vw;
  padding: 1% 0;
}

.about-container-right-img {
  position: absolute;
  left: 1vw;
  width: 30vw;
  padding-top: 15%;
  margin-bottom: 10%;
  background-color: rgba(0, 0, 0, 0.3);
  border-radius: 3px;
}


/* TEXT */

.about-text-description {
  font-family: "ComicR";
  font-size: 1.7vw;
  color: #fff;
  padding-bottom: 1.5vw;
}



/* SCREEN SIZE */

@media (max-width:768px){

  .about-app {
    position: relative;
    width: 100vw;
    height: 100%;
    background-color: #111010;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
  
  .about-container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .about-container-right {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding-right: 0%;
  }
  
  .about-container-left {
    position: relative;
    width: 100%;
    height: 100%;
    padding: 20% 2%;
    padding-bottom: 0;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
  
  
  /* CHINESE SIGN */
  
  .about-container-chn {
    display: none;
  }
  
  
  /* IMAGES */
  
  .about-container-right-img-bg {
    position: relative;
    width: 100vw;
    padding: 10% 0;
  }
  
  .about-container-right-img {
    position: absolute;
    left: 0;
    width: 100vw;
    padding-top: 15%;
    margin-bottom: 10%;
    background-color: none;
    border-radius: 3px;
  }
  
  
  /* TEXT */

  .about-text-description {
    font-family: "ComicR";
    font-size: 5vw;
    color: #fff;
    padding-bottom: 5vw;
    text-align: center;
  }
}