/* APP */

.home-app {
  position: relative;
  width: 100vw;
  height: 100vh;
  background-color: #111010;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.home-container {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  z-index: 2;
}

.home-container-left {
  position: absolute;
  top: 20%;
  left: 5%;
  width: 80%;
  height: 100%;
}

.home-container-right {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 50%;
  height: 100%;
  margin-right: 10%;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: center;
}


/* CHINESE SIGN */

.home-container-chn {
  position: absolute;
  height: 73%;
  right: 0.5vw;
  z-index: 2;
}


/* CHARACTERS */

.home-character-img {
  width: 100%;
}

.home-character-Center-img {
  width: 65%;
  z-index: 2;
}

.home-character-Left-img {
  position: absolute;
  bottom: 0;
  left: -10%;
  width: 55%;
  z-index: 1;
}

.home-character-Right-img {
  position: absolute;
  bottom: 0;
  right: -10%;
  width: 55%;
  z-index: 1;
}

.home-character-Left-task {
  position: absolute;
  bottom: 12%;
  left: -15%;
  width: 100%;
  z-index: 0;

  -webkit-animation-name: fadeOp;
  -webkit-animation-duration: 6s;
  -webkit-animation-iteration-count: infinite;
  animation-name: fadeOp;
  animation-duration: 6s;
  animation-iteration-count: infinite;
}

.home-character-Right-task {
  position: absolute;
  bottom: 12%;
  right: -15%;
  width: 100%;
  z-index: 0;

  -webkit-animation-name: fadeOp;
  -webkit-animation-duration: 5s;
  -webkit-animation-iteration-count: infinite;
  animation-name: fadeOp;
  animation-duration: 5s;
  animation-iteration-count: infinite;
}

@-webkit-keyframes fadeOp { 
  0% { opacity: 0.2; }
  50% { opacity: 0.7; }
  100% { opacity: 0.2; }
}

@keyframes fadeOp {
  0% { opacity: 0.2; }
  50% { opacity: 0.7; }
  100% { opacity: 0.2; }
}



/* TITLES  & BUTTON */

.home-container-titles-bottom {
  width: 100%;
  margin-bottom: 10vw;
}

.home-text-title-chn {
  font-size: 4vw;
  line-height: 5vw;
  color: #ddd;
  margin: 0;
  padding: 0;
  letter-spacing: 0px;
}

.home-text-title {
  font-size: 3vw;
  line-height: 5vw;
  color: #ddd;
  margin: 0;
  padding-left: 0.3vw;
  font-family: "Tokyo";
}

.home-text-description {
  width: 90%;
  font-size: 2vw;
  padding-left: 0.5vw;
  padding-top: 2vw;
  color: #eee;
  font-family: "ComicR";
}


/* RED LINE */

.home-red-line {
  position: absolute;
  background-color: rgba(150, 15, 13, 1.0);
  width: 100%;
  height: 10vw;
  bottom: 3vw;
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
  z-index: 1;
}

.home-red-line-text {
  max-width: 25vw;
  font-size: 2vw;
  line-height: 3vw;
  color: #fff;
  margin: 0;
  margin-left: 5vw;
  font-family: "ComicR";
}


/* BLACK LINE */

.home-footer-line {
  position: absolute;
  width: 100%;
  height: 2.5vw;
  bottom: 0;
  z-index: 5;
}



/* RANDOM TEXT */

.home-text-random {
  color: rgba(150, 15, 13, 0.8);
  font-size: 1.5vw;
  line-height: 2vw;
  z-index: 10;
  width: 1vw;

  -webkit-animation-name: textR;
  -webkit-animation-duration: 4s;
  -webkit-animation-iteration-count: infinite;
  animation-name: textR;
  animation-duration: 4s;
  animation-iteration-count: infinite;
}

@-webkit-keyframes textR { 
  0% { opacity: 0.0; }
  50% { opacity: 0.7; }
  100% { opacity: 0.0; }
}

@keyframes textR {
  0% { opacity: 0.0; }
  50% { opacity: 0.7; }
  100% { opacity: 0.0; }
}





