/* APP */

.updates-app {
  position: relative;
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
}

.updates-container-title {
  position: relative;
  width: 100%;
  padding: 10% 3% 0% 3%;
}

.updates-container-content {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  overflow: scroll;
}

.updates-container-left {
  position: relative;
  width: 35%;
  height: 100%;
  padding: 0 1%;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;

  overflow: scroll;
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

.updates-container-left::-webkit-scrollbar {
  display: none;
}

.updates-container-right {
  position: absolute;
  top: 5vw;
  right: 0;
  width: 65%;
  height: 100%;
  padding: 0 1%;
  overflow: scroll;

  display: flex;
  flex-direction: column;
  align-items: center;
}


/* TASK */

.updates-task-left {
  position: absolute;
  width: 40%;
  top: 40%;
  left: -5%;
  margin: 0;
  padding: 0;
  z-index: -1;
  opacity: 0.4;
}

.updates-task-right {
  position: absolute;
  width: 40%;
  top: 0%;
  right: -10%;
  margin: 0;
  padding: 0;
  z-index: -1;
  opacity: 0.7;
}


/* CARD */

.updates-card-container {
  position: relative;
  width: 30vw;
  height: 6vw;
  background-color: rgba(50, 50, 50, 0.5);
  border-radius: 2px;
  margin-bottom: 2%;
  padding: 0;
  overflow: hidden;
}

.updates-card-container:hover {
  background-color: rgba(150, 150, 150, 0.5);
}

.updates-card-container-selected {
  background-color: rgba(150, 150, 150, 0.5);
}

.updates-card-img {
  position: absolute;
  top: 0;
  left: 1%;
  height: 130%;
}

.updates-card-content {
  position: absolute;
  width: 65%;
  height: 50%;
  right: 0;
  bottom: 10%;
  z-index: 1;
  border-left: thick double rgba(0, 0, 0, 0.3);
}

.updates-card-title {
  position: absolute;
  top: 45%;
  left: 3%;

  font-family: "ComicR";
  font-size: 1.3vw;
  color: #ddd;
}

.updates-card-infos {
  position: absolute;
  top: 0;
  left: 3%;

  font-family: "Cavolini";
  font-size: 0.9vw;
  color: #ddd;
}


/* CONTENT CARD */

.updates-content-hide {
  display: none;
}

.updates-content-container {
  position: relative;
  width: 90%;
  margin-top: 10%;
  margin-bottom: 5%;
}

.updates-content-infos {
  width: 100%;
  padding: 1% 2%;
  margin: 0;
  background-color: rgba(50, 50, 50, 0.5);
  font-family: "Cavolini";
  font-size: 1.0vw;
  color: #ddd;
  border-bottom: thick double rgba(0, 0, 0, 0.3);
}

.updates-content-title {
  width: 100%;
  padding: 2%;
  margin: 0;
  background-color: rgba(50, 50, 50, 0.5);
  font-family: "ComicR";
  font-size: 1.5vw;
  color: #ddd;
}

.updates-content-text {
  width: 100%;
  padding: 1% 2%;
  margin: 0;
  background-color: rgba(50, 50, 50, 0.5);
  font-family: "Cavolini";
  font-size: 1.1vw;
  color: #ddd;
}

.updates-content-video {
  width: 100%;
  padding-top: 2vw;
}

.updates-content-url {
  width: 100%;
  padding: 1% 2%;
  margin: 0;
  background-color: rgba(50, 50, 50, 0.5);
  text-align: center;
}

.updates-content-urlText {
  font-family: "ComicR";
  font-size: 1.5vw;
  color: #ddd;
  text-decoration: none;
}

.updates-content-urlText:hover {
  color: rgba(150, 15, 13, 1.0);
}



/* SCREEN SIZE */

@media (max-width:768px){

  /* APP */

  .updates-app {
    position: relative;
    width: 100vw;
    height: 100vh;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
  }

  .updates-container-title {
    position: relative;
    width: 100%;
    padding: 2% 0% 5% 0%;
  }

  .updates-container-content {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
  }

  .updates-container-left {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 22vw;
    padding: 0 1%;
    background-color: black;
    z-index: 2;

    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
  }

  .updates-container-right {
    position: relative;
    width: 100%;
    height: 100%;
    padding: 0 1%;
    overflow: scroll;
    padding-bottom: 30%;
  }


  /* TASK */

  .updates-task-left {
    display: none;
  }

  .updates-task-right {
    position: absolute;
    width: 60%;
    top: -20%;
    right: -10%;
    margin: 0;
    padding: 0;
    z-index: -1;
    opacity: 0.7;
  }


  /* CARD */

  .updates-card-container {
    position: relative;
    width: 70vw;
    height: 20vw;
    background-color: rgba(50, 50, 50, 0.5);
    border-radius: 2px;
    margin: 1%;
    padding: 0;
    overflow: hidden;
  }

  .updates-card-container:hover {
    background-color: rgba(150, 150, 150, 0.5);
  }
  
  .updates-card-container-selected {
    background-color: rgba(150, 150, 150, 0.5);
  }

  .updates-card-img {
    display: none;
  }

  .updates-card-content {
    position: absolute;
    width: 90%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 1;
    border-left: 0 double rgba(0, 0, 0, 0.3);
  }

  .updates-card-title {
    position: absolute;
    top: 45%;
    left: 3%;

    font-family: "ComicR";
    font-size: 5vw;
    color: #ddd;
  }

  .updates-card-infos {
    position: absolute;
    top: 20%;
    left: 3%;

    font-family: "Cavolini";
    font-size: 4vw;
    color: #ddd;
  }


  /* CONTENT CARD */

  .updates-content-container {
    position: relative;
    width: 100%;
    margin-bottom: 5%;
  }

  .updates-content-infos {
    width: 100%;
    padding: 1% 2%;
    margin: 0;
    background-color: rgba(50, 50, 50, 0);
    font-family: "Cavolini";
    font-size: 4vw;
    color: #ddd;
    border-bottom: thick double rgba(250, 250, 250, 0.1);
  }

  .updates-content-title {
    width: 100%;
    padding: 2%;
    margin: 0;
    background-color: rgba(50, 50, 50, 0);
    font-family: "ComicR";
    font-size: 7vw;
    color: #ddd;
  }

  .updates-content-text {
    width: 100%;
    padding: 1% 2%;
    margin: 0;
    background-color: rgba(50, 50, 50, 0);
    font-family: "Cavolini";
    font-size: 4.5vw;
    color: #ddd;
  }

  .updates-content-video {
    width: 100%;
    padding-top: 2vw;
  }

  .updates-content-url {
    width: 100%;
    padding: 1% 2%;
    margin: 0;
    background-color: rgba(50, 50, 50, 0);
    text-align: center;
  }

  .updates-content-urlText {
    font-family: "ComicR";
    font-size: 5vw;
    color: #ddd;
    text-decoration: none;
  }

  .updates-content-urlText:hover {
    color: rgba(150, 15, 13, 1.0);
  }
}