/* APP */

.roadmap-container {
  position: relative;
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  
  background: url(../../assets/pictures/bg-roadmap.jpg);
  background-repeat: repeat;
  background-size: 14.3%;
}

.roadmap-container-title {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}


/* BACKGROUND */

.roadmap-bg-img {
  position: absolute;
  margin: 0;
  padding: 0;
  z-index: 1;
  opacity: 0.8;
}

.roadmap-paw {
  position: absolute;
  width: 1.5%;
  margin: 0;
  padding: 0;
  z-index: 1;
  opacity: 0.7;
}


/* QUARTER */

.roadmap-house {
  position: absolute;
  width: 15%;
  z-index: 2;
}

.roadmap-house-img {
  width: 100%;
  filter: brightness(70%);
}

.roadmap-house-quarter {
  position: absolute;
  top: 95%;
  left: 10%;
  width: 120%;
  font-family: "ComicR";
  font-size: 1.5vw;
  color: #ddd;
  display: flex;
}

.roadmap-house-quarter-short {
  font-family: "ComicR";
  font-size: 3vw;
  line-height: 3vw;
  padding-left: 0.5vw;
  color: #ddd;
}



/* QUARTER CARD */

.roadmap-house-description {
  display: none;
}

.roadmap-house-description-text {
  display: none;
}





/* HOVER ON DESKTOP ONLY */
@media (min-width:768px){

.roadmap-house:hover .roadmap-house-description {
  display: block;
  position: relative;
  padding: 0.5vw;
}

.roadmap-house:hover .roadmap-house-description-text {
  display: block;
  font-family: "ComicR";
  font-size: 0.9vw;
  line-height: 1vw;
  padding: 0.5vw 0;
  margin: 0;
  color: #ddd;
}

.roadmap-house:hover {
  min-height: 14%;
  z-index:100;
  transform:scale(1.5);
  transform-origin:50% 50%;
  background-color: rgba(0, 0, 0, 0.95);
  box-shadow: 0px 0px 10px #800505;
  border-radius: 2%;

  display: flex;
  align-items: center;
  justify-content: center;
}

.roadmap-house:hover .roadmap-house-img {
  position: absolute;
  opacity: 0.2;
  filter: brightness(100%);
  overflow: hidden;
}

.roadmap-house:hover .roadmap-house-quarter {
  top: 100%;
  left: 0;
}

}


/* SCREEN SIZE */
@media (max-width:768px){

  /* APP */

  .roadmap-container {
    background: url(../../assets/pictures/bg-roadmap.jpg);
    background-repeat: repeat;
    background-size: 40%;
  }

  /* BACKGROUND */

  .roadmap-bg-img {
    position: absolute;
    margin: 0;
    padding: 0;
    z-index: 1;
    opacity: 0.8;
  }

  .roadmap-paw {
    position: absolute;
    width: 4%;
    margin: 0;
    padding: 0;
    z-index: 1;
    opacity: 0.7;
  }


  /* QUARTER */

  .roadmap-house {
    position: absolute;
    width: 30%;
    z-index: 2;
  }

  .roadmap-house-quarter {
    position: absolute;
    top: 95%;
    left: 10%;
    width: 80%;
    font-family: "ComicR";
    font-size: 4vw;
    line-height: 1;
    color: #ddd;
    display: flex;
  }

  .roadmap-house-quarter-short {
    font-family: "ComicR";
    font-size: 8vw;
    line-height: 3vw;
    padding-left: 0.5vw;
    color: #ddd;
  }


  /* QUARTER CARD */

  .roadmap-house-description {
    display: block;
    position: absolute;
    top: 2%;
    left: 5%;
    width: 90%;
  }

  .roadmap-house-description-text {
    display: block;
    font-family: "ComicR";
    font-size: 5vw;
    line-height: 6vw;
    padding: 2vw 0;
    margin: 0;
    color: #ddd;
  }
  
  .roadmap-house-img-selected {
    position: relative;
    width: 100%;
    opacity: 0.1;
    filter: brightness(100%);
    overflow: hidden;
  }
}