.characters-app {
  position: relative;
  width: 100vw;
  height: 100%;
  margin: 0;
  padding: 0;
}

.characters-container {
  position: relative;
  text-align: center;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}


/* PARRALLAX */

.characters-char {
  position: relative;
  width: 100%;
  height: 50vw;
}

.characters-char-element {
  position: absolute;
  left: auto;
  top: auto;
  z-index: 5;
}

.characters-char-container {
  height: 5vw;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.characters-char-name {
  font-size: 3.4vw;
  color: #fff;
  margin: 0;
  padding: 0;
  font-family: "ComicR";
}

.characters-char-img-container {
  display: none;
}

.characters-char-img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  opacity: 0.5;
}


/* TEXT */

.characters-text-title {
  position: absolute;
  width: 45%;
  z-index: 2;
  font-size: 3.4vw;
  line-height: 5vw;
  color: #fff;
  margin: 0;
  font-family: "ComicR";
}


@media (max-width: 768px){

  .characters-app {
    position: relative;
    width: 100vw;
    height: 100%;
    margin: 0;
    padding: 0;
  }

  /* TEXT */
  .characters-container {
    position: relative;
    text-align: center;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  .characters-text-title {
    position: absolute;
    width: 90%;
    z-index: 2;
    font-size: 8vw;
    line-height: 10vw;
    color: #fff;
    margin: 0;
    font-family: "ComicR";
  }
  
  
  /* PARRALLAX */
  
  .characters-char {
    position: relative;
    width: 100%;
    height: 150vw;
  }
  
  .characters-char-element {
    position: absolute;
    left: auto;
    top: auto;
    z-index: 5;
  }
  
  .characters-char-container {
    height: 5vw;
  
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }
  
  .characters-char-name {
    font-size: 8vw;
    color: #fff;
    margin: 0;
    padding: 0;
    font-family: "ComicR";
  }
  
  .characters-char-img {
    position: absolute;
    top: 0;
    left: 0;
    width: 110%;
    opacity: 0.6;
  }
}



/* HOVER ON DESKTOP ONLY */
@media (min-width: 768px) {

  .characters-char-name:hover {
    font-size: 0vw;
    transform: scale(2);
  }
  
  .characters-char-name:hover .characters-char-img-container {
    display: block;
    position: relative;
    width: 10vw;
    height: 10vw;
    margin: auto;
  
    overflow: hidden;
  
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    background-color: rgba(10, 10, 10, 1);
    box-shadow: 0px 0px 15px #800505;
    border: solid 1px #800505;
    border-radius: 5px;
  }
}