/* FOOTER CONTAINER */

.footer-app {
  position: relative;
  width: 100%;
  height: 3vw;
  padding: 0;
  margin: 0;
}

.footer-container {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.footer-copyright {
  font-size: 0.7vw;
  line-height: 1;
  color: #ddd;
  font-family: "Cavolini";
  margin: 0;
  padding: 1vw;
}

.footer-terms-container {
  height: 1.25vw;
  margin: 0;
  padding: 1vw;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  border-radius: 2px;
}

.footer-terms-container:hover {
  background-color: rgba(250, 250, 250, 0.1);
}

.footer-terms-text {
  font-size: 0.8vw;
  line-height: 0;
  color: #ddd;
  margin: 0;
  padding-bottom: 1%;
  font-family: "Cavolini";
}



@media (max-width:768px){

  .footer-app {
    position: relative;
    width: 100%;
    height: 10vw;
    padding: 0;
    margin: 0;
  }
  
  .footer-copyright {
    font-size: 3vw;
    line-height: 1;
    color: #ddd;
    font-family: "Cavolini";
    margin: 0;
    padding: 1vw;
  }
  
  .footer-terms-container {
    height: 6vw;
    margin: 0;
    padding: 1vw;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    border-radius: 2px;
  }
  
  .footer-terms-text {
    font-size: 3vw;
    line-height: 0;
    color: #ddd;
    margin: 0;
    padding-bottom: 1%;
    font-family: "Cavolini";
  }
}