
/* TITLE */

.title-container {
  height: 5vw;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: row;
}
  
.title-text {
  font-family: "ComicR";
  font-size: 3vw;
  color: #eee;
  padding: 0;
  margin: 0;
}

.title-slash {
  font-family: "ComicI";
  font-size: 3.5vw;
  color: rgba(150, 15, 13, 1.0);
  padding: 0;
  margin: 0;
  transform: translate(-0.5vw, 0.25vw);
}

.title-description {
  height: 100%;
  font-family: "ComicR";
  font-weight: bold;
  font-size: 1.75vw;
  letter-spacing: 5px;
  color: rgba(150, 15, 13, 1.0);
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  transform: translate(-1vw, 0);
}



/* POPUP */

.hide {
  display: none;
  width: 0;
  height: 0;
}

.popup-text-background {
  display: none;
}

.popup-text-container {
  display: none;
}


/* CLOSE */

.close {
  position: fixed;
  bottom: 85%;
  width: 100%;
  z-index: 100;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.close-btn {
  width: 15vw;
  height: 15vw;
  background-color: rgba(0, 0, 0, 0.95);
  box-shadow: 0px 0px 15px #800505;
  border-radius: 50%;
  border: solid 2px #800505;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.close-text {
  font-family: "ComicR";
  font-size: 10vw;
  line-height: 10vw;
  padding-left: 1vw;
  padding-bottom: 2.5vw;
  margin: 0;
  color: #ddd;
}




@media (max-width: 768px){

  /* TITLE */

  .title-container {
    height: 20vw;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  .title-text {
    font-size: 10vw;
  }
  
  .title-slash {
    display: none;
  }
  
  .title-description {
    height: 100%;
    font-size: 6vw;
    transform: translate(0, -3vw);
  }


  /* POPUP */

  .hide {
    display: none;
    width: 0;
    height: 0;
  }

  .popup-text-background {
    display: block;
    position: fixed;
    bottom: 0;
    width: 100vw;
    height: 100vh;
    margin: 0;
    z-index: 10;

    background-color: rgba(0, 0, 0, 0.8);
    backdrop-filter: blur(15px);
  }

  .popup-text-container {
    display: block;
    position: fixed;
    bottom: 20%;
    width: 90vw;
    height: 60vh;
    margin: 0 5vw;
    z-index: 20;

    background-color: rgba(10, 10, 10, 1);
    box-shadow: 0px 0px 15px #800505;
    border: solid 1px #800505;
    border-radius: 5px;

    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    overflow: hidden;
  }


  /* CLOSE */

  .close {
    position: fixed;
    bottom: 6%;
    width: 100%;
    z-index: 100;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }

  .close-btn {
    width: 15vw;
    height: 15vw;
    background-color: rgba(0, 0, 0, 0.95);
    box-shadow: 0px 0px 15px #800505;
    border-radius: 50%;
    border: solid 2px #800505;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }

  .close-text {
    font-family: "ComicR";
    font-size: 10vw;
    line-height: 10vw;
    padding-left: 1vw;
    padding-bottom: 2.5vw;
    margin: 0;
    color: #ddd;
  }
}