/* APP */

.team-container {
  position: relative;
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  overflow: scroll;
}

.team-container-right {
  position: fixed;
  width: 45%;
  height: 100%;
  right: 0;
  bottom: 0;

  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: flex-end;
}

.team-container-left {
  position: relative;
  width: 100%;
  padding: 10% 2% 4% 2%;
  padding-right: 40%;
  z-index: 2;
}

.team-background {
  width: 95%;
}

.team-task {
  position: absolute;
  width: 180%;
  z-index: -1;
  transform: translate(25%, 20%);
  opacity: 0.4;
}


/* TITLES */

.team-title-intro {
  position: relative;
  width: 100%;
  padding: 4% 2%;
  margin: 0;
  font-family: "Cavolini";
  letter-spacing: -1px;
  font-size: 1.25vw;
  line-height: 1.5;
  color: #eee;
}


/* CARD */

.team-card-group {
  position: relative;
  width: 100%;
  padding: 0 2%;
  margin: 0;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  flex-wrap: wrap;
}

.team-card-container {
  position: relative;
  width: 12.9vw;
  height: 11vw;
  margin: 0.25vw;
  padding: 0;
  background-color: rgba(50, 50, 50, 0.8);
  background-repeat: no-repeat;
  background-size: cover;
  z-index: 1;
  overflow: hidden;
}

.team-card-img {
  width: 120%;
  margin: 0;
  padding: 0;
  opacity: 0.5;
  z-index: 1;
}

.team-card-name {
  position: absolute;
  left: 1.5vw;
  top: 0.5vw;
  width: 1vw;
  font-family: "ComicR";
  font-size: 1.25vw;
  line-height: 0.9;
  color: #fff;
  text-align: center;
  z-index: 2;
}

.team-card-name-jpn {
  position: absolute;
  left: 0.3vw;
  top: 0.5vw;
  width: 1vw;
  font-family: "ComicR";
  font-size: 1vw;
  line-height: 1;
  color: #fff;
  text-align: center;
  z-index: 2;
}

.team-card-job {
  position: absolute;
  right: 0.5vw;
  bottom: -0.5vw;
  font-family: "ComicR";
  font-size: 1.2vw;
  line-height: 1;
  color: #fff;
  z-index: 2;
}

.team-text-description {
  position: absolute;
  top: 0;
  right: 0;
  width: 78%;
  height: 100%;
  border-left: 5px double rgba(0, 0, 0, 0.3);
}

.team-card-twitter {
  display: none;
}



/* HOVER */

@media (min-width: 768px) {

  .team-card-container:hover .team-card-img {
    opacity: 0.1;
  }

  .team-card-container:hover .team-card-twitter {
    display: block;
    position: absolute;
    top: 35%;
    left: 35%;
    width: 30%;
  }
}


/* SCREEN SIZE */
@media (max-width: 768px){
  
  .team-container {
    position: relative;
    width: 100vw;
    height: 100%;
    margin: 0;
    overflow: scroll;
    padding-top: 20%;
  }

  .team-container-right {
    position: relative;
    width: 100%;
    height: 100%;
    padding-top: 10%;

    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: flex-end;

    overflow: hidden;
  }

  .team-container-left {
    position: relative;
    width: 100%;
    padding: 1% 2% 4% 2%;
    z-index: 2;
  }

  .team-background {
    position: absolute;
    bottom: 0;
    width: 100%;
  }

  .team-task {
    position: relative;
    width: 160%;
    z-index: -1;
    transform: translate(20%, 0%);
    opacity: 0.4;
  }


  /* TITLES */

  .team-title-intro {
    width: 100%;
    padding: 4% 2%;
    font-size: 5vw;
    line-height: 1.5;
    text-align: center;
  }


  /* CARD */

  .team-card-group {
    width: 100%;
    padding: 0 2%;
    margin: 0;
  }

  .team-card-container {
    position: relative;
    width: 100%;
    height: 75vw;
    margin: 5vw 0;
    padding: 0;
  }

  .team-card-img {
    width: 120%;
    margin: 0;
    padding: 0;
    opacity: 0.5;
    z-index: 1;
  }

  .team-card-name {
    position: absolute;
    left: 8vw;
    top: 1vw;
    width: 5vw;
    font-size: 6vw;
    line-height: 0.9;
  }

  .team-card-name-jpn {
    position: absolute;
    left: 1vw;
    top: 1vw;
    width: 5vw;
    font-size: 5vw;
    line-height: 1;
  }

  .team-card-job {
    position: absolute;
    right: 2vw;
    bottom: -0.5vw;
    font-size: 6vw;
    line-height: 1;
  }

  .team-text-description {
    width: 83%;
    border-left: 10px double rgba(0, 0, 0, 0.3);
  }
}

@media (max-height: 768px) and (min-width: 1368px) {

  .team-background {
    width: 90%;
    height: auto;
  }

  .team-container-right {
    right: 0;
    bottom: -5vw;
  }
}