.reader-app {
  position: relative;
  width: 100vw;
  height: 90vh;
  margin: 0;
  padding: 0;
  background-image: url("../../assets/mountains.png");
  background-repeat: no-repeat;
  background-size: cover;
}

.reader-container {
  width: 100%;
  height: 100%;
  padding: 1%;
}

.reader-container-right {
  position: relative;
  height: 100%;
  width: 100%;
  padding: 0;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.reader-container-left {
  position: relative;
  height: 100%;
  width: 100%;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}



/* TASK LEFT */

.reader-container-task-black {
  width: 16vw;
  z-index: 1;
}

.reader-container-task-chn {
  position: absolute;
  height: 22vw;
  padding-right: 2vw;
  z-index: 2;
}


/* TASK RIGHT */

.reader-container-task-text {
  position: relative;
  width: 60vw;
  z-index: 1;
}

.reader-container-task-text-logo {
  position: absolute;
  height: 5vw;
  top: 17.5vw;
  margin-left: 5vw;
  z-index: 2;
}

.reader-container-task-text-description {
  position: absolute;
  text-align: center;
  width: 25vw;
  bottom: 12vw;
  margin-left: 5vw;
  z-index: 2;
  font-size: 1.5vw;
  color: #fff;
  font-family: "ComicR";
  background-color: rgba(0, 0, 0, 0.4);
  border-radius: 10px;
}


/* RED LINE */

.reader-red-line {
  position: absolute;
  cursor: pointer;
  width: 10vw;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
  right: 0;
  top: 0;
  background-color: #DA0505;
  opacity: 0.7;
  z-index: 3;
}

.reader-red-line-text {
  font-size: 1vw;
  line-height: 2vw;
  color: #ffffff;
  margin: 0;
  font-family: "Tokyo";
}




@media (max-width:768px){

  .reader-app {
    position: relative;
    width: 100vw;
    height: 100%;
