/* MOBILE */

.navbar-mobile {
  display: block;
  z-index: 200;
  position: fixed;
  top: 0;
  width: 100%;
  height: 5vw;
}

.navbar-mobile-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0 2%;
}

.navbar-mobile-logo-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.navbar-mobile-logo-img {
  width: 3.5vw;
  border: solid 1px #fff;
  border-radius: 50%;
}

.navbar-mobile-logo-text {
  text-align: center;
  font-family: "ComicR";
  font-size: 2.5vw;
  color: #fff;
  margin: 0 1vw;
  line-height: 0;
}


/* MENU ICON */

.navbar-menu-icon {
  position: relative; 
  width: 4vw;
  height: 3vw;
  cursor: pointer;
  transition: transform 0.5s;
}

.navbar-menu-icon span {
  position: absolute;
  top: 50%;
  transform: translateY(-50%); 

  display: block;
  width: 100%;
  height: 0.4vw;
  transition: transform 0.5s;
}

.navbar-menu-icon span::before, .navbar-menu-icon span::after {
  content: '';
  position: absolute;
  top: 0;
  transform: translateY(-0.75vw);

  display: block;
  width: 100%;
  height: 100%;

  background-color: #eee;
  transition: transform 0.5s;
}

.navbar-menu-icon:hover span::before {
  background-color: #bbb;
}

.navbar-menu-icon:hover span::after {
  background-color: #bbb;
}

.navbar-menu-icon span::after {
  transform: translateY(0.75vw);
}

.navbar-menu-icon.is-opened span::before { 
  transform: translateY(0) rotate(45deg);
}

.navbar-menu-icon.is-opened span::after { 
  transform: translateY(0) rotate(-45deg);
}

.navbar-menu-icon.is-opened { 
  transform: rotate(90deg);
}


/* SCREEN SIZE */

@media (max-width: 768px){

  .navbar-mobile {
    top: 0vw;
    width: 100%;
    height: 15vw;
  }

  .navbar-mobile-container {
    padding: 0 5%;
  }
  
  .navbar-mobile-logo-img {
    width: 10vw;
  }

  .navbar-mobile-logo-text {
    display: none;
  }

  .navbar-menu-icon {
    position: relative; 
    width: 10vw;
    height: 10vw;
    cursor: pointer;
    transition: transform 0.5s;
  }

  .navbar-menu-icon span {
    position: absolute;
    top: 50%;
    transform: translateY(-50%); 

    display: block;
    width: 100%;
    height: 0.8vw;
    transition: transform 0.5s;
  }

  .navbar-menu-icon span::before, .navbar-menu-icon span::after {
    content: '';
    position: absolute;
    top: 0;
    transform: translateY(-2vw);

    display: block;
    width: 100%;
    height: 100%;

    background-color: #eee;
    transition: transform 0.5s;
  }

  .navbar-menu-icon span::after {
    transform: translateY(2vw);
  }

  .navbar-menu-icon.is-opened span::before { 
    transform: translateY(0) rotate(45deg);
  }

  .navbar-menu-icon.is-opened span::after { 
    transform: translateY(0) rotate(-45deg);
  }

  .navbar-menu-icon.is-opened { 
    transform: rotate(90deg);
  }

  .navbar-menu-icon.is-opened{
    stroke-dashoffset: 0; 
  }

}